.user-details {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(53, 61, 75, 0.2);
  visibility: visible;
  opacity: 1;
  transition: opacity 0.3s, visibility linear 0.3s;
}

.user-details__container {
  position: relative;
  width: 608px;
  min-height: 620px;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  color: black;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 10px;
}

.user-details__header {
  width: 100%;
  min-height: 56px;
  background-color: #353D4B;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  margin-bottom: 36px;
}

.user-details__title {
  font-family: 'Roboto', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: #ffffff;
  margin: 0;
}

.user-details__close-button {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 30px;
  height: 30px;
  border: none;
  padding: 0;
  outline: none;
  background-size: 30px 30px;
  background-position: center;
  background-color: rgba(0, 0, 0, 0);
  background-repeat: no-repeat;
}

.user-details__close-button:hover {
  cursor: pointer;
  opacity: 0.6;
}

.form {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
}

.form__label {
  font-family: 'Roboto', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #818C99;
  margin-bottom: 28px;
}

.form__label:nth-child(5) {
  margin-bottom: 28px;
}

.form_paragraph {
  font-family: 'Roboto', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #818C99;
  margin: 0 0 20px 0;
}

.form__input {
  font-family: 'Roboto', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  width: 480px;
  min-height: 36px;
  color: #353D4B;
  box-sizing: border-box;
  outline: none;
  margin-top: 4px;
  background: #F9FAFC;
  border: 2px solid #EBECEF;
  border-radius: 6px;
}

.form__button-block {
  background-color: #F6F7F9;
  width: 100%;
  min-height: 60px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.form__button {
  font-family: 'Roboto', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  width: 153px;
  min-height: 36px;
  background-color: #4997D0;
  border: none;
  border-radius: 6px;
  margin-right: 24px;
  color: #ffffff;
}

.form__button:hover {
  cursor: pointer;
  opacity: 0.6;
}